import * as React from "react";
import * as _ from "lodash";
import { withRouter, Redirect, Link } from "react-router-dom";
import {hot} from "react-hot-loader";
import { Button, Layout } from 'antd';
import { Drawer } from 'antd';
const { Content, Footer, Header } = Layout;
import { auth, firebase } from "../firebase";
import { useAuthState } from "react-firebase-hooks/auth";
import { useState } from 'react';
import { Modal } from 'antd';
import StyledFirebaseAuth from 'react-firebaseui/StyledFirebaseAuth';


const Menu = () => {
    const [user, loading, error] = useAuthState(auth);
    const [isLoginModalVisible, setIsLoginModalVisible] = useState(false);
    const [isCreateNewEventDrawerVisible, setIsCreateNewEventDrawerVisible] = useState(false);

    const uiConfig = {
        signInFlow: 'popup',
        signInSuccessUrl: '/',
        signInOptions: [
            firebase.auth.FacebookAuthProvider.PROVIDER_ID,
        ],
      };

    const loginClick = () => {
        setIsLoginModalVisible(true);
    }

    const closeLoginModal = () => {
        setIsLoginModalVisible(false);
    }

    const logoutClick = () => {
      auth.signOut();
    }

    const showHelp = () => {

    }

    const showCreateNewEventDrawer = () => {
      setIsCreateNewEventDrawerVisible(true);
    };

    const onCreateNewEventDrawerClose = () => {
      setIsCreateNewEventDrawerVisible(false);
    };

    let loginLink = (<Button type="link" onClick={loginClick}>Login</Button>);

    if(!loading && user !== null) {
        loginLink = (<><Link to={"/profile"}>Profile</Link><Button type="link" onClick={logoutClick}>Logout</Button></>);
    }

    let menu = (<><Button type="link" onClick={showCreateNewEventDrawer}>Create new event</Button><Button type="link" onClick={showHelp}>Help</Button>{loginLink}</>);
    if(loading) {
        menu = (<></>);
    }

    return (
        <>
            {menu}
            <Modal title="Login" 
                visible={isLoginModalVisible} 
                onOk={logoutClick} 
                onCancel={closeLoginModal}
                footer={[]}
                >
                    <StyledFirebaseAuth uiConfig={uiConfig} firebaseAuth={auth} />
                </Modal>
               <Drawer
                title="Create new event"
                placement="right"
                closable={false}
                onClose={onCreateNewEventDrawerClose}
                visible={isCreateNewEventDrawerVisible}
              >
                <p>Some contents...</p>
                <p>Some contents...</p>
                <p>Some contents...</p>
              </Drawer>
        </>
    );
}

export default hot(module)(withRouter(Menu));
