import * as React from "react";
import { Row, Col, Button } from 'antd';
import { useRef, useEffect, useState } from 'react';
import { hot } from "react-hot-loader";
import { withRouter, Redirect, Link } from "react-router-dom";
import { auth, firebase } from "../firebase";
import { useAuthState } from "react-firebase-hooks/auth";
import './Map.scss';
import { mapbox } from "../mapbox";
import 'mapbox-gl/dist/mapbox-gl.css';
import { Typography } from 'antd';
import { start } from "repl";

const { Title } = Typography;

type MapState = {
  lng: string,
  lat: string,
  zoom: string,
}

type EventState = {
  routeCoordinates: Array<Number>,
  clickCoordinates: Array<Number>,
  clicks: number,
  routeInstructions: Map<number, Object>,
}

function getDefaultEventState(): EventState {
  return {
    routeCoordinates: [],
    clickCoordinates: [],
    clicks: 0,
    routeInstructions: new Map<number, Object>(),
  }
}

type PageState = {
  isCreatingEvent: boolean,
  canCreateEvent: boolean,
}

//https://github.com/firebase/firebaseui-web-react
//https://github.com/alex3165/react-mapbox-gl
const MapView = () => {
  const eventCreationZoomMinLevel = 11.7;

  const [user, loading, error] = useAuthState(auth);

  const mapContainerRef = useRef(null);

  // const [lng, setLng] = useState(5);
  // const [lat, setLat] = useState(34);
  // const [zoom, setZoom] = useState(1.5);
  const [lng, setLng] = useState(-122.2946);
  const [lat, setLat] = useState(37.1787);
  const [zoom, setZoom] = useState(14);

  const [mapBox, setMapBox] = useState(null);

  const [pageState, setPageState] = useState<PageState>({
    isCreatingEvent: false,
    canCreateEvent: false
  });

  const [eventState, setEventState] = useState<EventState>(getDefaultEventState());

  // an arbitrary start will always be the same
  // only the end or destination will change
  //var start = [-122.662323, 45.523751];

  function getFeatureCollectionDescription(coords) {
    return {
      'type': 'FeatureCollection',
      'features': [
        {
          'type': 'Feature',
          'properties': {},
          'geometry': {
            'type': 'Point',
            'coordinates': coords
          }
        }
      ]
    };
  }

  function getMapLayerDescription(name, coords, color) {
    return {
      'id': name,
      'type': 'circle',
      'source': {
        'type': 'geojson',
        'data': getFeatureCollectionDescription(coords),
      },
      'paint': {
        'circle-radius': 10,
        'circle-color': color
      }
    };
  }

  function lngLatDictToArray(coordsObj) {
    var coords = Object.keys(coordsObj).map(function (key) {
      return coordsObj[key];
    });
    return coords;
  }

  function onMapMove(map, e) {
    setLng(map.getCenter().lng.toFixed(4));
    setLat(map.getCenter().lat.toFixed(4));
    setZoom(map.getZoom().toFixed(2));

    if (eventCreationZoomMinLevel < zoom) {
      pageState.canCreateEvent = true;
    }
    else {
      pageState.canCreateEvent = false;
    }
    setPageState(pageState);

    // console.log(map.getCenter().lng.toFixed(4));
    // console.log(map.getCenter().lat.toFixed(4));
    // console.log(map.getZoom().toFixed(2));
  }

  function getRoute(map, end) {

    eventState.clickCoordinates.push(end[0]);
    eventState.clickCoordinates.push(end[1]);

    const clicksCount = eventState.clickCoordinates.length;
    const prevPoint = clicksCount == 2 ? clicksCount - 1 : clicksCount - 3;

    // make directions request using cycling profile
    var url =
      'https://api.mapbox.com/directions/v5/mapbox/walking/' +
      eventState.clickCoordinates[prevPoint - 1] +
      ',' +
      eventState.clickCoordinates[prevPoint] +
      ';' +
      end[0] +
      ',' +
      end[1] +
      '?steps=true&geometries=geojson&access_token=' +
      mapbox.accessToken;

    // make an XHR request https://developer.mozilla.org/en-US/docs/Web/API/XMLHttpRequest
    var req = new XMLHttpRequest();
    req.open('GET', url, true);
    req.onload = function () {
      var json = JSON.parse(req.response);
      var data = json.routes[0];
      var route = data.geometry.coordinates;

      for (const r in route) {
        eventState.routeCoordinates.push(route[r]);
      }

      var geojson = {
        'type': 'Feature',
        'properties': {},
        'geometry': {
          'type': 'LineString',
          'coordinates': eventState.routeCoordinates
        }
      };
      // if the route already exists on the map, we'll reset it using setData
      if (map.getSource('route')) {
        map.getSource('route').setData(geojson);
      }
      // otherwise, we'll make a new request
      else {
        map.addLayer({
          'id': 'route',
          'type': 'line',
          'source': {
            'type': 'geojson',
            'data': {
              'type': 'Feature',
              'properties': {},
              'geometry': {
                'type': 'LineString',
                'coordinates': eventState.routeCoordinates
              }
            }
          },
          'layout': {
            'line-join': 'round',
            'line-cap': 'round'
          },
          'paint': {
            'line-color': '#3887be',
            'line-width': 5,
            'line-opacity': 0.75
          }
        });
      }

      eventState.clicks = eventState.clicks + 1;
      eventState.routeInstructions.set(eventState.clicks, data.legs[0]);

      setEventState({
        clickCoordinates: eventState.clickCoordinates,
        routeCoordinates: eventState.routeCoordinates,
        clicks: eventState.clicks,
        routeInstructions: eventState.routeInstructions,
      });

      console.log(eventState);

      //console.log(data);
      // get the sidebar and add the instructions
      //var instructions = document.getElementById('instructions');
      //var steps = data.legs[0].steps;

      // var tripInstructions = [];
      // for (var i = 0; i < steps.length; i++) {
      //   tripInstructions.push('<br><li>' + steps[i].maneuver.instruction) +
      //     '</li>';
      //   //instructions.innerHTML =
      //   console.log(
      //     '<br><span class="duration">Trip duration: ' +
      //     Math.floor(data.duration / 60) +
      //     ' min ðŸš´ </span>' +
      //     tripInstructions);
      // }
    };
    req.send();
  }

  function onMapClick(map, e) {
    if (eventCreationZoomMinLevel < map.getZoom()) {
      if (!pageState.isCreatingEvent) {
        setEventState(getDefaultEventState());
      }

      pageState.isCreatingEvent = true;
      setPageState(pageState);
      // console.log(e);

      // var coordinates = e.lngLat;

      // // Ensure that if the map is zoomed out such that multiple
      // // copies of the feature are visible, the popup appears
      // // over the copy being pointed to.
      // while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
      //   coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
      // }
      var coords = lngLatDictToArray(e.lngLat);
      map.getCanvasContainer().style.cursor = '';

      if (map.getLayer('end')) {
        var end = getFeatureCollectionDescription(coords);
        map.getSource('end').setData(end);
      } else {
        map.addLayer(getMapLayerDescription('start', coords, '#f30'));
        map.addLayer(getMapLayerDescription('end', [], '#3887be'));
      }
      getRoute(map, coords);
    }
  }

  // Initialize map when component mounts
  useEffect(() => {
    const map = new mapbox.Map({
      container: mapContainerRef.current,
      //style: 'mapbox://styles/mapbox/outdoors-v11',
      style: {
        version: 8,
        sources: {
          osm: {
            type: 'raster',
            tiles: ["https://tile.openstreetmap.org/{z}/{x}/{y}.png"],
            tileSize: 256,
            attribution: 'Map tiles by <a target="_top" rel="noopener" href="https://tile.openstreetmap.org/">OpenStreetMap tile servers</a>, under the <a target="_top" rel="noopener" href="https://operations.osmfoundation.org/policies/tiles/">tile usage policy</a>. Data by <a target="_top" rel="noopener" href="http://openstreetmap.org">OpenStreetMap</a>'
          }
        },
        layers: [{
          id: 'osm',
          type: 'raster',
          source: 'osm',
        }]
      },
      center: [lng, lat],
      zoom: zoom
    });
    setMapBox(map);

    map.on('move', (e) => onMapMove(map, e));
    map.on('click', (e) => onMapClick(map, e));

    // 3D
    // map.on('load', function () {
    //   map.addSource('mapbox-dem', {
    //   'type': 'raster-dem',
    //   'url': 'mapbox://mapbox.mapbox-terrain-dem-v1',
    //   'tileSize': 512,
    //   'maxzoom': 14
    //   });
    //   // add the DEM source as a terrain layer with exaggerated height
    //   map.setTerrain({ 'source': 'mapbox-dem', 'exaggeration': 1.5 });

    //   // add a sky layer that will show when the map is highly pitched
    //   map.addLayer({
    //   'id': 'sky',
    //   'type': 'sky',
    //   'paint': {
    //     'sky-type': 'atmosphere',
    //     'sky-atmosphere-sun': [0.0, 0.0],
    //     'sky-atmosphere-sun-intensity': 15
    //     }
    //   });
    // });

    map.addControl(new mapbox.NavigationControl());
    map.addControl(new mapbox.ScaleControl({ position: 'bottom-right' }));

    // Clean up on unmount
    return () => map.remove();
  }, []);

  function cancelEventCreationHandler() {
    pageState.isCreatingEvent = false;
    setPageState(pageState);

    eventState.clickCoordinates = [];
    eventState.clicks = 0;
    eventState.routeCoordinates = [];
    eventState.routeInstructions = new Map<number, Object>();
    setEventState(eventState);

    mapBox.removeLayer('route');
    mapBox.removeSource('route');

    mapBox.removeLayer('start');
    mapBox.removeSource('start');

    mapBox.removeLayer('end');
    mapBox.removeSource('end');
  }

  function saveEventHandler() {

  }

  let body = (<></>);
  if (pageState.isCreatingEvent) {
    body = (
      <Row>
        <Col span={24}>
          <Title level={2}>New event</Title>
        </Col>
        <Col span={24}>
          <Button onClick={cancelEventCreationHandler}>Cancel</Button>
          <Button onClick={saveEventHandler}>Save</Button>
        </Col>
      </Row>
    );
  } else {
    if (pageState.canCreateEvent) {
      body = (
        <Row>
          <Col span={24}>
            <Title level={2}>Explore upcoming hikes or Click to create your event</Title>
          </Col>
        </Row>
      );
    }
    else {
      body = (
        <Row>
          <Col span={24}>
            <Title level={2}>Explore upcoming hikes or Zoom in to create your event</Title>
          </Col>
        </Row>
      );
    }
  }

  return (
    <>
      <Row>
        <Col span={18}>
          <div className='map-container' ref={mapContainerRef} />
        </Col>
        <Col span={6}>
          {body}
        </Col>
      </Row>
    </>
  );
}

export default hot(module)(withRouter(MapView));
